<template>
	<div class="container">
		<v-back title="消息回复" />
		<div class="scroll">
			<div class="list" v-for="(item, index) in list" :key="index">
				
				
				<div class="flex align_center" v-if="item.from_user_id == userId" style="justify-content: flex-end;">
					<h1 style="margin: 0 2vw;text-align: right;">
						{{item.from_user_name}}（{{typeText(item.from.usertype)}}）
						<div v-show="item.send_location" style="font-size: 3vw;color: #000;padding-top: 1vw;font-weight: normal;"><md-icon name="location" size="sm"></md-icon>{{item.send_location}}</div>
					</h1>
					<img :src="item.from.photo" alt="" class="header" @click="imgsShow(item.from.photo)">
				</div>
				<div class="flex align_center" v-else>
					<img :src="item.from.photo" alt="" class="header" @click="imgsShow(item.from.photo)">
					<h1 style="margin: 0 2vw;">
						{{item.from_user_name}}（{{typeText(item.from.usertype)}}）
						<div v-show="item.send_location" style="font-size: 3vw;color: #000;padding-top: 1vw;font-weight: normal;"><md-icon name="location" size="sm"></md-icon>{{item.send_location}}</div>
					</h1>
				</div>
				
				<div class="flex" v-if="item.from_user_id == userId" style="justify-content: flex-end;align-items: flex-end;">
					<span style="color: #ccc;padding: 1vw;min-width: 6vw;">{{item.read_flag == '0' ? '未读' : '已读'}}</span>
					<h1 class="content">
						{{item.content}}
						<img v-if="item.attach_url" style="display: block;" :src="item.attach_url" alt="" @click="imgsShow(item.attach_url)">
					</h1>
					<div class="space"></div>
				</div>
				<div class="flex" v-else style="align-items: flex-end;">
					<div class="space"></div>
					<h1 class="content">
						{{item.content}}
						<img v-if="item.attach_url" style="display: block;" :src="item.attach_url" alt="" @click="imgsShow(item.attach_url)">
					</h1>
					<span style="color: #ccc;padding: 1vw;min-width: 6vw;">{{item.read_flag == '0' ? '未读' : '已读'}}</span>
				</div>
				<h2 class="center" style="font-weight: normal;">{{item.send_time}}</h2>
			</div>
		</div>
		<div class="print_box" v-show="!formShow" v-if="data.from.id == userId || data.to.id == userId">
			<div class="input_box">
				<div class="input" @click="formShow = true">回复</div>
			</div>
		</div>
		<div v-else class="center" style="line-height: 10vw;font-size: 3.6vw;">只有【{{data.from.username}}】与【{{data.to.username}}】才能相互通信</div>
		<div v-show="formShow" class="form">
			<md-field>
				<div class="md-field-item is-solid">
					<div class="md-field-item-content" style="padding: 0;">
						<label class="md-field-item-title">证明图片</label>
						<div class="md-field-item-control">
							<ul class="image-reader-list">
								<li
								class="image-reader-item"
								v-for="(img, index) in imageList['tupian']"
								:key="index"
								:style="{
									'backgroundImage': `url(${img})`,
									'backgroundPosition': 'center center',
									'backgroundRepeat': 'no-repeat',
									'backgroundSize': 'cover'
								}"
								@click="imgsShow(img)">
								<md-tag
									class="image-reader-item-del"
									size="small"
									shape="quarter"
									fill-color="#111A34"
									type="fill"
									font-color="#fff"
									@click.native.stop="onDeleteImage('tupian', index)"
								>
									<md-icon name="close"></md-icon>
								</md-tag>
								</li>
								<li class="image-reader-item add" v-show="imageList.tupian.length == 0">
									<md-image-reader
										name="tupian"
										@select="onReaderSelect"
										@complete="onReaderComplete"
										@error="onReaderError"
									>
									</md-image-reader>
									<md-icon name="camera" size="md" color="#CCC"></md-icon>
									<p>添加图片</p>
								</li>
							</ul>
						</div>
					</div>
				</div>
				<md-input-item
				  ref="input0"
				  title="* 回复内容"
				  placeholder="请输入回复内容"
				  is-amount
				  v-model="content">
				</md-input-item>
				<div class="flex">
					<div class="submit" @click="submit">发送</div>
					<div class="cancle" @click="formShow = false">取消</div>
				</div>
				
			</md-field>
		</div>
		<!-- 图片预览 -->
		<md-image-viewer
			v-model="isViewerShow"
			:list="imgs"
			:has-dots="true">
		</md-image-viewer>
	</div>
</template>

<script>
	import axios from 'axios'
	import compressImage from 'ls-compressimage'
	export default {
		name: 'news_detail',
		data() {
			return {
				userId: JSON.parse(localStorage.getItem('user')).user_id,
				user: JSON.parse(localStorage.getItem('user')),
				data: {
					from: {},
					to: {}
				},
				list: [],
				formShow: false,
				
				imageList: {
					tupian: []
				},
				content: '',
				blob: null,
				file: {},
				
				isViewerShow: false,
				imgs: []
			}
		},
		methods: {
			typeText(usertype) {
				if (usertype == 'S') {
					return '学生'
				} else if (usertype == 'T') {
					return '老师'
				} else if (usertype == 'V') {
					return '访客'
				} else if (usertype == 'Y') {
					return '黄名单人员'
				} else if (usertype == 'U') {
					return '系统管理员'
				} else if (usertype == 'I') {
					return '陌生人'
				} else {
					return '其他'
				}
			},
			imgsShow(img) {
				this.isViewerShow = true
				this.imgs = [img]
			},
			getList(callback) {
				this.$getL('/news/list', {
				    userId: this.userId,
					person_entry_exit_id: this.$route.query.id
				}, (res)=>{
					let data = res.data
					this.data = data[0]
					this.list = res.data[0].list.reverse()
					setTimeout(()=> {
						this.goScrollFooter()
					}, 200)
					
					this.$toast.hide()
					if (callback) callback()
				})
			},
			onReaderSelect(name, {files}) {
			    this.$toast.loading('图片读取中...')
			},
			onReaderComplete(name, {dataUrl, blob, file}) {
			    this.$toast.hide()
			    setTimeout(() => {
			        const demoImageList = this.imageList[name] || []
			        demoImageList.push(dataUrl)
			        this.$set(this.imageList, name, demoImageList)
					this.blob = blob
					this.file = file
			    }, 100)
			},
			onReaderError(name, {msg}) {
			    this.$toast.failed(msg)
			},
			onDeleteImage(name, index) {
			    const demoImageList = this.imageList[name] || []
			    demoImageList.splice(index, 1)
			    this.$set(this.imageList, name, demoImageList)
			},
			getPositionAddress(callback) {
				let _this = this;
				if (_this.user.usertype == 'T') { //老师不需要定位信息
					if (callback) callback('')
					return;
				}
				//学生需要定位信息
				_this.$toast.loading('位置获取中...')
				var geolocation = new BMap.Geolocation();
				var geoc = new BMap.Geocoder();
				geolocation.getCurrentPosition(function(r){
					if(this.getStatus() == BMAP_STATUS_SUCCESS) {
						geoc.getLocation(r.point, function(rs){
							var addComp = rs.addressComponents;
							var address = addComp.province + "" + addComp.city + "" + addComp.district + "" + addComp.street + "" + addComp.streetNumber
							if (callback) callback(address)
						});    
					}
					else {
						alert('failed'+this.getStatus());
					}        
				},{enableHighAccuracy: true})
			},
			submit() { //提交
				if (this.content == '') {
					this.$toast.failed('请输入回复内容')
					return;
				}
				if (this.blob == null || this.imageList.tupian.length == 0) {
					this.$toast.loading('数据提交中...');
					this.getPositionAddress((address) => {
						this.submit_form(address, '')
					})
				} else {
					this.$toast.loading('图片上传中...');
					new compressImage({
						res: this.file, // 必选 资源
						justConversion: false, // 可选 声明了此字段说明只进行格式转换 res requestType responseType callback必填，其他随意
						width: 300, // 可选(justConversion存在可不选) (默认图片自身宽度,建议自定义) 压缩后图片宽度
						//height: 300,//可选 (默认 自定义图片宽度/原生图片宽度*原生图片高度) 压缩后图片高度
						quality: 0.1, // 可选 (默认0.3) 压缩层级质量（0<quality<1）越大压缩越快 图片质量跨度相对低 越小则相反
						size: 500, // 必选(justConversion存在可不选) 压缩后图片大小 单位k
						requestType: 'file', // 必选 传入文件类别 base64 | file | blob
						responseType: 'file', // 必选 返回文件类别 base64 | file | blob
						fileName: 'person', // 如果responseType的类型为file和非justConversion 此选项可选 返回文件对象的文件名
						callback: (result) => {
							if (result === null) {
								this.$toast.failed("图片上传失败")
								return
							}
							var formData = new FormData();
							formData.append("file", result, new Date().getTime() + '.' + result.type.split('/')[1]);
							axios.post('http://106.12.3.212/dorm/visitor/UploadImg/-1', formData, { headers: {'Content-Type': 'multipart/form-data'} }).then( res => {
								  if (res.data.code == 0) {
									  setTimeout(() => {
										  this.$toast.loading('数据提交中...');
										  this.getPositionAddress((address) => {
											this.submit_form(address, res.data.url)
										  })
									  })
								  } else {
									  this.$toast.failed("图片上传失败")
								  }
							}).catch((err) => {
								this.$toast.failed("图片上传失败")
							})
						}
					})
				}
			},
			submit_form(address, attach_url) { //上传
				let toUser = {}
				if (this.data.from.id == this.userId) {
					toUser = this.data.to
				} else {
					toUser = this.data.from
				}
				let params = {
					person_entry_exit_id: this.$route.query.id,
					content: this.content,
					attach_url: attach_url,
					send_location: address,
					from_user_id: this.user.user_id,
					from_user_nbr: this.user.usernbr,
					from_user_name: this.user.username,
					to_user_id: toUser.id,
					to_user_nbr: toUser.usernbr,
					to_user_name: toUser.username
				}
				
				this.$post('/news/form', params, (result)=>{
					axios.post('http://106.12.3.212/dormApi/api/chatrecord/pushInteractNotification', {
						'msgId': result.data.msg_id
					}, {
					    transformRequest: [function (data) {
					        let ret = ''
					        for (let it in data) { ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&' }
					        return ret
					    }],
					    headers: {
							'Content-Type': 'application/x-www-form-urlencoded'
						},
					    withCredentials: false,
						timeout: 1000*30 //30秒超时限制
					}).then(res => {
						console.log(res.data.code)
						if (res.data.code == 0) {
							this.$toast.succeed("发送成功")
							setTimeout(() => {
								this.formShow = false
								this.imageList.tupian = []
								this.content = ''
								this.getList();
							}, 500)
						} else {
							this.$toast.failed("推送消息失败")
						}
					}).catch(err => {
						this.$toast.failed("推送消息失败")
					})
				})
			},
			
			readFlagChange(array) { //改变为已读
				this.$post('/news/read_flag_change', {
					array: JSON.stringify(array)
				}, ()=>{
				    this.getList()
				})
			},
			goScrollFooter() { //滚动到底部
				this.$nextTick(() => {
					var container = this.$el.querySelector('.scroll')
					container.scrollTop = container.scrollHeight
			  })
			}
		},
		mounted() {
			this.getList(() => {
				let arrayParams = []
				this.list.forEach(item => {
					if (item.to_user_id == this.userId && item.read_flag == '0') {
						arrayParams.push({
							msg_id: item.msg_id,
							read_flag: '1'
						})
					}
				})
				if (arrayParams.length > 0) {
					this.readFlagChange(arrayParams)
				}
			});
		}
	}
</script>

<style scoped="scoped">
	.scroll {
		box-sizing: border-box;
		height: calc(100vh - 26vw);
		overflow-y: auto;
		background-color: #fff;
		padding: 4vw;
	}
	.list {
		box-sizing: border-box;
		padding: 2vw 0;
		border-bottom: 1px solid rgba(0,0,0,.03);
	}
	.header {
		width: 10vw;
		height: 10vw;
		border-radius: 50%;
	}
	.content {
		background-color: #1acfc6;
		color: #fff;
		padding: 2vw;
		border-radius: 1vw;
		margin: 1vw 0;
		font-weight: normal;
		box-shadow: 0 0 5px rgba(0,0,0,.2);
	}
	.content img {
		max-width: 30vw;
		box-sizing: border-box;
		margin-top: 1vw;
		background-color: #fff;
	}
	.space {
		width: 11vw;
	}
	.print_box {
		height: 12vw;
		background-color: #fff;
	}
	.input_box {
		box-sizing: border-box;
		height: 12vw;
		padding: 2vw;
	}
	.input {
		height: 8vw;
		background-color: #1acfc6;
		border-radius: 4vw;
		box-shadow: 2px 2px 5px rgba(0,0,0,.2);
		text-align: center;
		line-height: 8vw;
		color: #fff;
		font-size: 3.8vw;
	}
	.form {
		background-color: #fff;
		height: calc(50vh - 20vw);
		border-top: 2vw solid #1acfc6;
		position: fixed;
		width: 100%;
		bottom: 0;
		z-index: 2;
	}
	.image-reader-list {
	     width: 100%;
	     margin-top: 3vw;
		 clear: both;
	 }
	.image-reader-list .image-reader-item {
	    position: relative;
	    width: 25vw;
	    height: 25vw;
	    padding-bottom: 23.5%;
	    margin-bottom: 2%;
	    margin-right: 2%;
	    background: #FFF;
	    box-shadow: 0 5px 20px rgba(197, 202, 213, .25);
	    box-sizing: border-box;
	    list-style: none;
	    border-radius: 4px;
	    background-size: cover;
	    overflow: hidden;
	}
	.image-reader-list .image-reader-item .md-tag {
	     position: absolute;
	     right: 0;
	 }
	 .image-reader-item.add .md-icon {
	     position: absolute;
	     top: 40%!important;
	     left: 50%;
	     transform: translate(-50%, -50%);
	     opacity: .5;
	 }
	.image-reader-item.add p {
	     position: absolute;
	     top: 35%;
	     left: 0;
	     width: 100%;
	     margin-top: 15px;
	     font-size: 22px;
	     color: #CCC;
	     text-align: center;
	     font-size: 12px;
	 }
	 .get_position {
		 color: #fff;
		 background-color: #1acfc6;
		 display: inline-block;
		 padding: 2vw;
		 margin: 3vw 0;
		 font-size: 3.6vw;
	 }
	 .submit {
		 height: 8vw;
		 background-color: #1acfc6;
		 border-radius: 4vw;
		 box-shadow: 2px 2px 5px rgba(0,0,0,.2);
		 text-align: center;
		 line-height: 8vw;
		 color: #fff;
		 font-size: 3.8vw;
		 flex: 1;
		 margin-right: 1vw;
		 margin-top: 2vw;
	 }
	 .cancle {
		 height: 8vw;
		 background-color: #fff;
		 border-radius: 4vw;
		 box-shadow: 2px 2px 5px rgba(0,0,0,.2);
		 text-align: center;
		 line-height: 8vw;
		 color: #1acfc6;
		 font-size: 3.8vw;
		 flex: 1;
		 margin-left: 1vw;
		 margin-top: 2vw;
	 }
</style>
